export default Index

function RequireAuth({ children }) {
  var authed = localStorage.getItem("admin_token") !== undefined && localStorage.getItem("admin_token") !== null && localStorage.getItem("admin_token") !== '' ? true : false;
  return authed === true ? (
    children
  ) : (
    <Navigate to="/" replace  />
  );
}
