import { Image } from 'antd'
import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet'
import ReactPaginate from 'react-paginate'
import { useSelector } from 'react-redux'
import BASE_URL from '../API/Config'
import Container from '../Component/Container'

const ManageCompaint = () => {
    const [complian , setCompain] = useState([])
    const [pageData, setPageData] = useState(0)

// const token = useSelector(state => state.admin.adminlogintoken)
const token = localStorage.getItem('admin_token')

    const CustomerComplain = async() => {
        const view = await axios.get(`${BASE_URL}viewComplaint?pageSize=10&pageNumber=1`,{
            headers: {
                "Authorization": `Bearer ${token}`
            }
        })
        console.log(view.data.data)
        setCompain(view.data.data.rows)
        const Count = view.data.data.count
         console.log(Count)
         setPageData(Math.ceil(Count / 10))
    }

    useEffect(() => {
        CustomerComplain()
    },[])

    const fecthComments = async (CurrentPage) => {
      const res = await fetch(
          `${BASE_URL}viewComplaint?pageSize=10&pageNumber=${CurrentPage}`, {
          headers: {
              "Authorization": `Bearer ${token}`
          },
  
          data: {
            pageSize: 10 ,
            pageNumber: CurrentPage
          }
      }
      );
      const data = await res.json();
      console.log(data)
      const d = data.data.rows;
      return d;
  }
  const Click = async (d) => {
      window.scrollTo(0, 0);
      var CurrentPage = d.selected + 1;
      const Comments = await fecthComments(CurrentPage);
      setCompain(Comments)
  };

  return (
    <Container>
       <Helmet>
        <meta charSet="utf-8" />
        <title>Car Washing | Manage Complaint</title>
      </Helmet>
      <div className="page-wrapper">
        <div className="page-header d-print-none">
          <div className="container-xl">
            <div className="row g-2 align-items-center">
              <div className="col">
                <h2 className="page-title">
                Manage Complaints
                </h2>
              </div>         
              <div className="col-12">
                <div className="card">
                  <div className="card-header">
                    <h3 className="card-title">Tasks</h3>
                  </div>
                  
                  <div className="table-responsive">
                    <table className="table card-table table-vcenter text-nowrap datatable">
                      <thead>
                        <tr>
                          <th className="w-1">S No.</th>
                          <th>Image</th>
                          <th>Description</th>
                          <th>Status</th>
                          <th>Created Date</th>
                         <th>Updated Date</th>                         
                          </tr>
                      </thead>
                        {complian.map((check , index) => {
                            return (
                              <tbody key={index}>
                            <tr>
                              <td>{index+1}</td>
                              <td> <Image src={`https://tgu90.com:8000/complaintImg/${check.image}`} alt='...' width={60} height={60} /></td>
                              <td>{check.description}</td>
                              <td>{check.status}</td>
                              <td>{check.created_at}</td>                            
                              <td>{check.updated_at}</td>                         
                            </tr>                   
                         </tbody>  
                            )
                        })}                       
                    </table>
                  </div>                 
                </div>
              </div>
            </div>
            <ReactPaginate
                    style={{ background: 'red' }}
                    previousLabel={"Previous"}
                    nextLabel={'Next'}
                    breakLabel={"..."}
                    pageCount={pageData}
                    marginPagesDisplayed={3}
                    pageRangeDisplayed={3}
                    onPageChange={Click}
                    containerClassName={'pagination  justify-content-center m-3'}
                    pageClassName={'page-item'}
                    pageLinkClassName={'page-link'}
                    previousClassName={'page-item'}
                    previousLinkClassName={'page-link'}
                    nextClassName={'page-item'}
                    nextLinkClassName={'page-link'}
                    breakClassName={"page-item"}
                    breakLinkClassName={'page-link'}
                    activeClassName={'active'}
                />
          </div>
        </div>       
      </div>

    </Container>
  )
}

export default ManageCompaint
