import axios from "axios"
import BASE_URL from "../Config"

const getProfileData = async (token) => {
    try {
        const res = await axios.get(`${BASE_URL}adminProfile`, { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

const updateProfileData = async (token, data) => {
    try {
        const res = await axios.post(`${BASE_URL}updateadminProfile`, (data), { headers: { "Authorization": `Bearer ${token}` } })
        return res
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

const changePasswordData = async (token,data) => {
    try {
        const res = await axios.post(`${BASE_URL}change_password`, (data), { headers: { "Authorization": `Bearer ${token}` } })
        return res
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

export { getProfileData, updateProfileData, changePasswordData }