import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { Link, useNavigate } from 'react-router-dom'
import { toast, ToastContainer } from 'react-toastify'
import BASE_URL from '../API/Config'


const NewPasswordToast = () => {
  toast.success("Password Changed Successfully")
}

const ResetPassword = () => {
  // const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [confirm, setConfirm] = useState('')

  const navigate = useNavigate()

  const token = useSelector(state => state.admin.adminlogintoken)
  const selector = useSelector((state) => state.forgot.userEmail)

  const resetpass = () => {
    axios({
      url: `${BASE_URL}forget_password`,
      method: 'post',
      data: {
        email: selector
      }
    })
  }

  const reset = (e) => {
    e.preventDefault()
    axios({
      url: `${BASE_URL}resetPassword`,
      method: 'post',
      headers: {
        "Authorization": `Bearer ${token}`
      },
      data: {
        email: selector,
        password: password,
        confirmation_password: confirm
      }
    }).then((response) => {
      console.log(response.status)
      NewPasswordToast()
      if (response.status === 200) {
        setTimeout(() => {
          navigate('/')
        }, 2000)
      }


    }).catch((error) => {
      console.log(error)
    })

  }

  useEffect(() => {
    resetpass()
  }, [])

  return (

    <div className="page page-center">
      <ToastContainer
        autoClose={1000}
        position="top-center"
        hideProgressBar
        className="toast-container"
        toastClassName="dark-toast"
        theme="colored"
        toastStyle={{ backgroundColor: '#1a48aa' }}
      />
      <div className="container container-tight py-4">
        <div className="text-center mb-2">
          <a href="." className="navbar-brand ">
            <img src='bluescarwash.png' width={240} height={240} alt='' /></a>
        </div>
        <form className="card card-md" onSubmit={reset}>
          <div className="card-body">
            <h2 className="card-title text-center mb-3">Reset password</h2>

            <div className="mb-3">
              <label className="form-label">Email address</label>
              <input type="email" className="form-control" placeholder="Enter email" name='email' value={selector} /* onChange={(e) =>setEmail(e.target.value)}  */ />
            </div>
            <div className="mb-3">
              <label className="form-label">Enter Password</label>
              <input type="password" className="form-control" placeholder="Enter Password" name='password' onChange={(e) => setPassword(e.target.value)} />
            </div>
            <div className="mb-3">
              <label className="form-label">Confirm Password</label>
              <input type="password" className="form-control" placeholder="Confirm Password" name='confirm' onChange={(e) => setConfirm(e.target.value)} />
            </div>
            <div className="form-footer">
              <button className="btn  w-100" style={{ backgroundColor: '#1a48aa', color: 'white' }}>
                Reset Password
              </button>
            </div>
          </div>
        </form>
        <div className="text-center text-muted mt-3">
          Forget it, <Link to="/" style={{ color: '#1a48aa' }}>send me back</Link> to the Log in screen.
        </div>
      </div>
    </div>
  )
}

export default ResetPassword
