import { Image, Modal } from 'antd'
import { Formik, Form, Field } from 'formik'
import React, { useEffect, useRef, useState } from 'react'
import { Helmet } from 'react-helmet'
import { addOffer, deleteOffer, getOffersList } from '../API/Offers'
import Container from '../Component/Container'
import * as Yup from 'yup';
import { toast, Toaster } from 'react-hot-toast'
import { IconX } from '@tabler/icons-react'

const ManageOffers = () => {
    const [offersData, setOffersData] = useState([]);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [imageAdd, setImageAdd] = useState('');
    const [deleteId, setDeleteId] = useState()
    const [addImage, setAddImage] = useState();
    const token = localStorage.getItem('admin_token')
    const inputRef = useRef();

    const showModal = () => {
        setIsModalOpen(true);
    };

    const handleOk = () => {
        setIsModalOpen(false);
    };

    const handleCancel = () => {
        setIsModalOpen(false);
    };

    const getOffersData = async () => {
        try {
            const total = await getOffersList(token)
            console.log('getOffersList', total)
            setOffersData(total.data)
        } catch (error) {
            console.log(error)
        }
    }

    const deleteOfferData = async () => {
        try {
            const total = await deleteOffer(token, deleteId)
            console.log('deleteOffer', total)
            deleteToast();
            getOffersData();
        } catch (error) {
            console.log(error)
        }
    }
    
    useEffect(() => {
        getOffersData();
    }, [])

    const SignupSchema = Yup.object().shape({
        title: Yup.string()
            .required('Please enter title'),
        slider_type: Yup.string()
            .required('Please enter slider type'),
        // image: Yup.string()
        //     .nullable()
        //     .required('Please add image'),
    });

    const OfferAddToast = () => {
        toast.success('Offer Added Successfully.', {
            style: {
                border: '1px solid #1a48aa',
                padding: '16px',
                color: '#1a48aa',
            },
            iconTheme: {
                primary: '#1a48aa',
                secondary: '#FFFAEE',
            },
        });
    }

    const deleteToast = () => {
        toast.success('Offer Deleted Successfully.', {
            style: {
                border: '1px solid #1a48aa',
                padding: '16px',
                color: '#1a48aa',
            },
            iconTheme: {
                primary: '#1a48aa',
                secondary: '#FFFAEE',
            },
        });
    }

    const addhandleChange = (e) => {
        if (e.target.files && e.target.files.length > 0) {
            setAddImage(e.target.files[0]);
        } else {
            setAddImage();
        }
    }

    return (
        <Container>
            <Helmet>
                <meta charSet="utf-8" />
                <title>Car Washing | Manage Offers</title>
            </Helmet>
            <Toaster
                position="top-right"
                reverseOrder={false}
            />
            <div className="page-wrapper">
                <div className="page-header d-print-none">
                    <div className="container-xl">
                        <div className="row g-2 align-items-center">
                            <div className="col">
                                <h2 className="page-title">
                                    Manage Offers
                                </h2>
                            </div>
                            <div className="col-auto ms-auto d-print-none">
                                <div className="btn-list">
                                    <button className="btn  d-none d-sm-inline-block" onClick={showModal} style={{ backgroundColor: '#1a48aa', color: 'white' }} >
                                        <svg xmlns="http://www.w3.org/2000/svg" className="icon" width="24" height="24" viewBox="0 0 24 24"
                                            strokeWidth="2" stroke="#ffffff" fill="none" strokeLinecap="round" strokeLinejoin="round">
                                            <path stroke="none" d="M0 0h24v24H0z" fill="none" /><path d="M12 5l0 14" /><path d="M5 12l14 0" /></svg>
                                        Add Offers
                                    </button>
                                    <button className="btn  d-sm-none btn-icon" onClick={showModal} aria-label="Create new report"
                                        style={{ backgroundColor: '#1a48aa', color: 'white' }}>
                                        <svg xmlns="http://www.w3.org/2000/svg" className="icon" width="24" height="24" viewBox="0 0 24 24"
                                            strokeWidth="2" stroke="currentColor" fill="none" strokeLinecap="round" strokeLinejoin="round">
                                            <path stroke="none" d="M0 0h24v24H0z" fill="none" /><path d="M12 5l0 14" /><path d="M5 12l14 0" /></svg>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="page-body">
                    <div className="container-xl">
                        <div className="row row-cards">
                            <div className="col-12">
                                <div className="card">
                                    <div className="card-header">
                                        <h3 className="card-title">Offers Data</h3>
                                    </div>
                                    <div className="table-responsive">
                                    </div>
                                    <div className="table-responsive">
                                        <table className="table card-table table-vcenter text-nowrap datatable" style={{ textTransform: 'capitalize' }}>
                                            <thead>
                                                <tr>
                                                    <th className="w-1">S No. </th>
                                                    <th>Image</th>
                                                    <th>Title</th>
                                                    <th>Slider Type</th>
                                                    <th>Created Date</th>
                                                    <th></th>
                                                </tr>
                                            </thead>
                                            {offersData.map((item, index) => {
                                                // let serial_num = ((10 * (pageNumber - 1)) + index + 1);
                                                var timezone = new Date(item.created_at).toLocaleString(undefined, { timeZone: 'Asia/Kolkata', hour12: true });
                                                return (
                                                    <tbody key={index}>
                                                        <tr>
                                                            <td>{index + 1}</td>
                                                            <td><Image src={`https://tgu90.com:8000/sliderImg/${item.image}`} alt='...' width={100} height={60}></Image></td>
                                                            <td>{item.title}</td>
                                                            <td>{item.slider_type}</td>
                                                            <td style={{textTransform:'uppercase'}}>{timezone}</td>
                                                            <td><button className='btn btn-outline-danger w-60' onClick={() => setDeleteId(item.id)}
                                                                data-bs-toggle="modal" data-bs-target="#modal-danger">Delete</button></td>
                                                        </tr>
                                                    </tbody>
                                                )
                                            })}
                                        </table>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>


                {/* Add Offer Modal Statrt */}
                <Modal title="Add New Offer" open={isModalOpen} onOk={handleOk} onCancel={handleCancel} footer={null} width={700} className="hello">

                    <div className="modal-dialog modal-lg" role="document">
                        <div className="modal-content">
                            <Formik
                                initialValues={{
                                    title: '',
                                    image: '',
                                    slider_type: '',
                                }}

                                validationSchema={SignupSchema}
                                onSubmit={async (value, { resetForm }) => {
                                    const formData = new FormData();
                                    formData.append("title", value.title)
                                    formData.append("slider_type", value.slider_type)
                                    formData.append("image", imageAdd)
                                    try {
                                        const total = await addOffer(token, formData)
                                        console.log('addOffer', total)
                                        setIsModalOpen(false);
                                        resetForm({ values: '' });
                                        OfferAddToast()
                                        getOffersData();
                                    } catch (error) {
                                        console.log('addOffer', error)
                                    }
                                }
                                }
                            >
                                {({ errors, touched }) => (
                                    <Form noValidate >
                                        <div className='row'>
                                            <div className="col-lg-12">
                                                <div className="m-1">
                                                    <label className="form-label">Title</label>
                                                    <Field type="text" className="form-control" name="title" />
                                                </div>
                                                {errors.title && touched.title ?
                                                    <div className="" style={{ color: '#9D0305', fontSize: '14px', margin: '8px' }}>{errors.title}</div> :
                                                    null}
                                            </div>
                                            <div className="col-lg-12">
                                                <div className="m-1">
                                                    <label className="form-label">Slider Type</label>
                                                    <Field type="text" className="form-control" name="slider_type" />
                                                </div>
                                                {errors.slider_type && touched.slider_type ?
                                                    <div className="" style={{ color: '#9D0305', fontSize: '14px', margin: '8px' }}>{errors.slider_type}</div> :
                                                    null}
                                            </div>
                                            <div className="col-lg-12">
                                                <div className="m-1">
                                                    <label className="form-label">Image</label>
                                                    <p onChange={addhandleChange}>
                                                        <Field type="file" className="form-control" name="imageAdd" ref={inputRef} onChange={(e) => setImageAdd(e.currentTarget.files[0])} style={{ border: '1px dashed rgb(26, 72, 170)' }} aria-label="File browser example" />
                                                    </p>
                                                </div>
                                                {!addImage ?
                                                    // <Image src={show} alt="/" width={50} height={50} /> 
                                                    <div></div>
                                                    :
                                                    <div style={{ display: 'inline-block', position: 'relative' }}>
                                                        <Image
                                                            src={URL.createObjectURL(addImage)} width={120} height={100}
                                                            alt="..."
                                                        />
                                                        <IconX onClick={() => { setAddImage(); inputRef.current.value = ""; }} style={{ position: 'absolute', top: '-12px', right: '-12px', color: "rgb(26, 72, 170)", cursor: 'pointer' }} />
                                                    </div>
                                                }
                                                {/* {errors.image && touched.image ?
                                                    <div className="" style={{ color: '#9D0305', fontSize: '14px', margin: '8px' }}>{errors.image}</div> :
                                                    null} */}
                                            </div>
                                        </div>
                                        <div className="d-grid gap-2 col-12 mt-auto " style={{ justifyContent: 'end' }}>
                                            <div className="modal-footer " style={{ marginTop: '20px' }}>
                                                <button type="button" className="btn me-auto  text-light" onClick={handleCancel}
                                                    style={{ backgroundColor: '#1a48aa', color: 'white', width: '170px' }}>Close</button>
                                                <button type="submit" className="btn me-auto  text-light" /* onClick={handleCancel} */
                                                    style={{ backgroundColor: '#1a48aa', color: 'white', width: '170px' }} > Add Offer </button>
                                            </div>
                                        </div>
                                    </Form>
                                )}
                            </Formik>
                        </div>
                    </div>
                </Modal>
                {/* Add Offer Modal End */}

                {/* Delete Offer Modal Start */}
                <div className="modal modal-blur fade" id="modal-danger" tabIndex={-1} role="dialog" aria-hidden="true">
                    <div className="modal-dialog modal-sm modal-dialog-centered" role="document">
                        <div className="modal-content">
                            <button type="button" className="btn-close " data-bs-dismiss="modal" aria-label="Close" />
                            <div className="modal-status " style={{ backgroundColor: '#1a48aa' }} />
                            <div className="modal-body text-center py-4">
                                <svg xmlns="http://www.w3.org/2000/svg" className="icon mb-2 icon-lg" style={{ color: '#1a48aa' }} width={24} height={24} viewBox="0 0 24 24" strokeWidth={2} stroke="currentColor" fill="none" strokeLinecap="round" strokeLinejoin="round"><path stroke="none" d="M0 0h24v24H0z" fill="none" /><path d="M12 9v2m0 4v.01" /><path d="M5 19h14a2 2 0 0 0 1.84 -2.75l-7.1 -12.25a2 2 0 0 0 -3.5 0l-7.1 12.25a2 2 0 0 0 1.75 2.75" /></svg>
                                <h3>Are you sure?</h3>
                                <div className="text-muted">Do you really want to remove this Offer Data? What you've done cannot be undone.</div>
                            </div>
                            <div className="modal-footer">
                                <div className="w-100">
                                    <div className="row">
                                        <div className="col"><a href="#" className="btn w-100" data-bs-dismiss="modal" style={{ backgroundColor: '#1a48aa', color: 'white' }}>
                                            Cancel
                                        </a></div>
                                        <div className="col"><button className="btn btn-danger w-100" data-bs-dismiss="modal"
                                            onClick={() => {
                                                deleteOfferData()
                                            }}
                                        >
                                            Delete
                                        </button></div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                {/* Delete  Offer Modal End*/}
            </div>
        </Container>
    )
}

export default ManageOffers
