import axios from "axios"
import BASE_URL from "../Config"


const getPaymentList = async (token, pageNumber, pageSize) => {
    try {
        const res = await axios.get(`${BASE_URL}view_payment?pageNumber=${pageNumber}&pageSize=${pageSize}`, { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

export { getPaymentList }