import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet'
import { useSelector } from 'react-redux'
// import { toast, ToastContainer } from 'react-toastify'
import BASE_URL from '../../API/Config'
import Container from '../../Component/Container'
import toast, { Toaster } from 'react-hot-toast';


const uploadAbouttoast = () => {
  // toast.success("About Us Changed Successfully")
  toast.success('Terms and Condition added successfuly', {
    style: {
      border: '1px solid #1a48aa',
      padding: '16px',
      color: '#1a48aa',
    },
    iconTheme: {
      primary: '#1a48aa',
      secondary: '#FFFAEE',
    },
  });
}

const ErrorAbouttoast = () => {
  // toast.error("Description is Required")
  toast.error('Description is Required.', {
    style: {
      border: '1px solid #1a48aa',
      padding: '16px',
      color: '#1a48aa',
    },
    iconTheme: {
      primary: '#1a48aa',
      secondary: '#FFFAEE',
    },
  });
}

const TitleAbouttoast = () => {
  // toast.error("Title is Required")
  toast.error('Title is Required.', {
    style: {
      border: '1px solid #1a48aa',
      padding: '16px',
      color: '#1a48aa',
    },
    iconTheme: {
      primary: '#1a48aa',
      secondary: '#FFFAEE',
    },
  });
}

const TermsUse = () => {
  const [title, setTitle] = useState("")
  const [description, setDescription] = useState("")
  const [page, setPage] = useState("")
  const [valid , setValidate] = useState('')

  // const token = useSelector(state => state.admin.adminlogintoken)
  const token = localStorage.getItem('admin_token')


  const viewTerms = () => {
    axios({
      url: `${BASE_URL}get_terms`,
      method: 'get',
      headers: {
        "Authorization": `Bearer ${token}`
      }
    }).then((response) => {
      console.log(response.data.data)
      setDescription(response.data.data.description)
      setTitle(response.data.data.title)
      setPage(response.data.data.page_type)
      setValidate(response.data.data)

    }).catch((error) => {
      console.log(error)
    })
  }

  const viewTermsUpdate = (e) => {
    e.preventDefault()
    const data = {
      title: title,
      description: description,
      page_type: 2
    }
    axios({
      url: `${BASE_URL}add_terms`,
      method: 'post',
      headers: {
        "Authorization": `Bearer ${token}`
      },
      data: data
    }).then((response) => {
      console.log(response)
      console.log(response.data)
      uploadAbouttoast()

    }).catch((error) => {
      console.log(error)
      if (error.response.data.error.message == "description is required") {
        ErrorAbouttoast()
      } else {
        TitleAbouttoast()
      }
    })
  }

  useEffect(() => {
    viewTerms()
  }, [])

  return (
    <Container>
       <Helmet>
        <meta charSet="utf-8" />
        <title>Car Washing | Terms of Use</title>
      </Helmet>
       <Toaster
  position="top-right"
  reverseOrder={false}
/>
      {/* <ToastContainer
        autoClose={1000}
        position="top-center"
        hideProgressBar
        className="toast-container"
        toastClassName="dark-toast"
        theme="colored"
        toastStyle={{ backgroundColor: '#1a48aa' }}
      /> */}
      <Helmet>
        <meta charSet="utf-8" />
        <title> Terms of Use</title>
      </Helmet>
      <div className="page-wrapper">
        <div className="page-header d-print-none">
          <div className="container-xl">
            <div className="row g-2 align-items-center">
              <div className="col">
                <h2 className="page-title">
                  Terms of Use
                </h2>
              </div>
            </div>
          </div>
        </div>
        <div className="page page-center ">
          <div className="container container-xl py-4 ">
            <form className="card card-md " onSubmit={viewTermsUpdate}>
              <div className="card-body ">
                <div className="mb-3">
                  <label className="form-label">Title</label>
                  <input type="text" className="form-control" placeholder="Enter Title" name='title' value={title} onChange={(e) => setTitle(e.target.value)} />
                </div>
                <div className="mb-3">
                  <label className="form-label">Description</label>
                  <textarea className="form-control" rows="4" placeholder="Enter Description" value={description} name='description'
                    onChange={(e) => setDescription(e.target.value)}></textarea>
                </div>

                <div className="form-footer">
                  {valid.title === title && valid.description === description ? <span className='not-allowed'>
                  <button  className="btn w-40" style={{ backgroundColor: '#1a48aa', color: 'white' }} disabled >Send</button>
                  </span> :  <button type="submit" className="btn w-40" style={{ backgroundColor: '#1a48aa', color: 'white' }}>Send</button> }
                 
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>

    </Container>
  )
}

export default TermsUse
