import axios from "axios"
import BASE_URL from "../Config"


const getOffersList = async (token) => {
    try {
        const res = await axios.get(`${BASE_URL}slider-list`, { headers: { 'Authorization': `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error);
    }
}

const addOffer = async (token, data) => {
    try {
        const res = await axios.post(`${BASE_URL}slider-add`, (data), { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

const deleteOffer = async (token, id) => {
    try {
        const res = await axios.delete(`${BASE_URL}slider-delete/${id}`, { headers: { "Authorization": `Bearer ${token}` } })
        return res
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

export { getOffersList, addOffer ,deleteOffer}