import axios from "axios"
import BASE_URL from "../Config"


const getMembershipList = async (token) => {
    try {
        const res = await axios.get(`${BASE_URL}membership-list`, { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

const addMembership = async (token,data) => {
    try {
        const res = await axios.post(`${BASE_URL}membership-add`, (data), { headers: { "Authorization": `Bearer ${token}` } })
        return res
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

const updateMembershipData = async (token, data) => {
    try {
        const res = await axios.post(`${BASE_URL}membership-update`, (data), { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

const deleteMembershipData = async (token, id) => {
    try {
        const res = await axios.delete(`${BASE_URL}membership-delete/${id}`, { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

export { getMembershipList, updateMembershipData, deleteMembershipData, addMembership }