import axios from "axios"
import BASE_URL from "../Config"


const getContactUsList = async (token, totalSize, pageNumber) => {
    try {
        const res = await axios.get(`${BASE_URL}contact_us?pageSize=${totalSize}&pageNumber=${pageNumber}`, { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

const changeStatus = async (token, data) => {
    try {
        const res = await axios.post(`${BASE_URL}contactus-change-status`, (data), { headers: { "Authorization": `Bearer ${token}` } })
        return res
    } catch (error) {
        console.log(error)
        throw (error)
    }
}
export { getContactUsList, changeStatus }