import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet'
import { useSelector } from 'react-redux'
import BASE_URL from '../../API/Config'
import Container from '../../Component/Container'
// import 'react-toastify/dist/ReactToastify.css';
// import { toast, ToastContainer } from 'react-toastify';
import toast, { Toaster } from 'react-hot-toast';


const uploadAbouttoast = () => {
  // toast.success("About Us Changed Successfully")
  toast.success('About Us Changed Successfully.', {
    style: {
      border: '1px solid #1a48aa',
      padding: '16px',
      color: '#1a48aa',
    },
    iconTheme: {
      primary: '#1a48aa',
      secondary: '#FFFAEE',
    },
  });
}

const Errortermtoast = () => {
  // toast.error("Description is Required")
  toast.error('Description is Required.', {
    style: {
      border: '1px solid #1a48aa',
      padding: '16px',
      color: '#1a48aa',
    },
    iconTheme: {
      primary: '#1a48aa',
      secondary: '#FFFAEE',
    },
  });
}

const Titletermtoast = () => {
  // toast.error("Title is Required")
  toast.error('Title is Required.', {
    style: {
      border: '1px solid #1a48aa',
      padding: '16px',
      color: '#1a48aa',
    },
    iconTheme: {
      primary: '#1a48aa',
      secondary: '#FFFAEE',
    },
  });
}

const Aboutus = () => {
  const [title , setTitle] = useState("")
  const [description , setDescription] = useState("")
  const [page, setPage] = useState("")
  const [validate , setValidate] = useState('')

  // const token = useSelector(state => state.admin.adminlogintoken)
  const token = localStorage.getItem('admin_token')

  const view = () => {
    axios({
     url: `${BASE_URL}get_about_us`,
     method: 'get',
     headers: {
      "Authorization": `Bearer ${token}`
     }
    }).then((response) => {
      console.log(response.data.data)
     
      setDescription(response.data.data.description)
      setTitle(response.data.data.title)
      setPage(response.data.data.page_type)
      setValidate(response.data.data)

    }).catch((error) => {
      console.log(error)
    })
  }


  const viewUpdate = (e) => {
    e.preventDefault()
    const data = {
      title: title,
      description: description,
      page_type: 1
    }
    axios({
      url: `${BASE_URL}about_us`,
      method: 'post',
      headers: {
        "Authorization": `Bearer ${token}`
      },
      data: data
    }).then((response) => {
      console.log(response)
      uploadAbouttoast()
    }).catch((error) => {
      console.log(error)
      if(error.response.data.error.message == "description is required"){
        Errortermtoast()
      } else {
        Titletermtoast()
      }
    })
  }

  useEffect(() => {
    view()
  }, [])


  // useEffect(() => {
  //   about()
  //   if (Object.keys(formErrors).length === 0 && isSubmit) {
      
  //   }

  // });
  // const validate = (data) => {
  //   const errors = {}
  
  //   if (!data.title) {
  //     errors.title = "Email is Required ! ";
  //   } 
  //   if (!data.description) {
  //     errors.description = "Description is Required ! ";
  //   }

  //   return errors;
  // }


  return (
    <Container>
       <Toaster
  position="top-right"
  reverseOrder={false}
/>
      {/* <ToastContainer
    autoClose={1000}
    position="top-center"
    hideProgressBar
    className="toast-container"
    toastClassName="dark-toast"
    theme="colored"
    toastStyle={{ backgroundColor: '#1a48aa' }}
  /> */}
         <Helmet>
        <meta charSet="utf-8" />
        <title>Car Washing | About Us</title>
      </Helmet>
      <div className="page-wrapper">
      <div className="page-header d-print-none">
        <div className="container-xl">
          <div className="row g-2 align-items-center">
            <div className="col">
              <h2 className="page-title">
               About Us
              </h2>
            </div>       
            <div className="col-12">
              <div className="card">                           
              </div>
            </div>
          </div>
        </div>
      </div>
   <div className="page page-center " >
  <div className="container container-xl py-4 ">
  <form className="card card-md " onSubmit={viewUpdate}>
      <div className="card-body ">     
        <div className="mb-3">
          <label className="form-label">Title</label>
          <input type="text" className="form-control" placeholder="Enter Title" name='title' value={title} onChange={(e) => setTitle(e.target.value)} />
        </div>    
                <div className="mb-3">
                  <label className="form-label">Description</label>
                  <textarea className="form-control" rows="4" placeholder="Enter Description" value={description} name='description'
                  onChange={(e) => setDescription(e.target.value)}></textarea>
                </div>           
        <div className="form-footer">
          {validate.title === title && validate.description === description ? <span className='not-allowed'>
           <button type="submit" className="btn w-40 " style={{backgroundColor: '#1a48aa' , color:'white'}} disabled>Send</button></span> : 
           <button type="submit" className="btn w-40"  style={{backgroundColor: '#1a48aa' , color:'white'}}>Send</button>  }
          {/* <button type="submit" className="btn w-40" style={{backgroundColor: '#1a48aa' , color:'white'}}>Send</button> */}
        </div>
      </div>
    </form>
  </div>
</div>
    </div>

  </Container>
  )
}

export default Aboutus
