const deleteCustomer = async (token, id) => {
    try {
        const res = await axios.delete(`${BASE_URL}deleteResidents/${id}`, { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

export { getTotalApiDetails, getCustomerList, getCustomerById, updateCustomer,addResidents, customerRegistrationsControl ,deleteCustomer }