import axios from 'axios'
import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, useNavigate } from 'react-router-dom'
import BASE_URL from '../API/Config'
import { forgot } from './EmailSlice'
import toast, { Toaster } from 'react-hot-toast';


const AdminSuccessToast = () => {
  // toast.success('Successfull Login.')
  toast.success('OTP send successfully on your Registered Email ID.', {
    style: {
      border: '1px solid #1a48aa',
      padding: '16px',
      color: '#1a48aa',
    },
    iconTheme: {
      primary: '#1a48aa',
      secondary: '#FFFAEE',
    },
  });
    }

    const AdminErrorToast = () => {
      // toast.success('Successfull Login.')
      toast.success('Sorry!! This Email ID does not Exist.', {
        style: {
          border: '1px solid #1a48aa',
          padding: '16px',
          color: '#1a48aa',
        },
        iconTheme: {
          primary: '#1a48aa',
          secondary: '#FFFAEE',
        },
      });
        }

const ForgotPassword = () => {
  const [email, setEmail] = useState('')

  const navigate = useNavigate()
  const dispatch = useDispatch();

  // const token = useSelector(state => state.admin.adminlogintoken)

  const forgotpassword = (e) => {
    
    const Email = email
    dispatch(forgot(Email))
    e.preventDefault()
    axios({
      url: `${BASE_URL}forget_password`,
      method: 'post',
      // headers: {
      //   "Authorization": `Bearer ${token}`
      // },
      data: {
        email: email
      }
    }).then((response) => {
      console.log(response)
     
      AdminSuccessToast()
      setTimeout(() => {
       navigate('/AdminOtp')
      },2000)
     
    }).catch((error) => {
      console.log(error)
     
      AdminErrorToast()
      console.log(error.response.data.message)

    })
  }
  return (
    <div className="page page-center">
       <Toaster
        position="top-right"
        reverseOrder={false}
      />
      <div className="container container-tight py-4">
        <div className="text-center mb-2">
          <a href="." className="navbar-brand ">
            <img src='bluescarwash.png' width={240} height={240} alt='' /></a>
        </div>
        <form className="card card-md" onSubmit={forgotpassword} >
          <div className="card-body">
            <h2 className="card-title text-center mb-3">Reset Password</h2>
            <p className="text-muted mb-3">Enter your Registered Email Address.</p>
            <div className="mb-3">
              <label className="form-label">Email address</label>
              <input type="email" className="form-control" placeholder="Enter email" name='email' onChange={(e) => setEmail(e.target.value)} />
            </div>
            <div className="form-footer">
              <button className="btn  w-100" style={{ backgroundColor: '#1a48aa', color: 'white' }}>
                {/* <svg xmlns="http://www.w3.org/2000/svg" className="icon" width={24} height={24} viewBox="0 0 24 24" strokeWidth={2} 
            stroke="currentColor" fill="none" strokeLinecap="round" strokeLinejoin="round"><path stroke="none" d="M0 0h24v24H0z" fill="none" />
            <path d="M3 5m0 2a2 2 0 0 1 2 -2h14a2 2 0 0 1 2 2v10a2 2 0 0 1 -2 2h-14a2 2 0 0 1 -2 -2z" /><path d="M3 7l9 6l9 -6" /></svg> */}
                Reset Password
              </button>
            </div>
          </div>
        </form>
        <div className="text-center text-muted mt-3">
          Forget it, <Link to="/" style={{ color: '#1a48aa' }}>send me back</Link> to the sign in screen.
        </div>
      </div>
    </div>

  )
}

export default ForgotPassword
